._1vanO {
    position: relative;
    width: 3rem;
    height: 2rem;
    
    background-color: #bebebe;
    border-radius: 15px;
    
    display: flex;
    align-items: center;
    
    padding: 0 5px;
    outline: none;
}

._1vanO input[type="checkbox"] {
    position: absolute;
    width: 1rem;
    height: 1rem;
    z-index: 10;
    opacity: 0;
    cursor: pointer;
}
._1MzwC {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 15px;
}
._1vanO > ._1nNhD {
    background-color: #d35041;
}
._1vanO input:checked ~ ._1nNhD {
    background-color: #47b25a;
}
._1MzwC:before {
    position: absolute;
    content: "";
    height: 1.6rem;
    width: 1.6rem;
    left: 5%;
    bottom: 10%;
    background-color: white;
    transition: .4s;
    border-radius: 50%;

    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.4);
}

input:checked + ._1MzwC:before {
    transform: translateX(105%);
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.4);
}
input:checked {
    transform: translateX(105%);
}
input:checked + ._1MzwC {
    background-color: #2196f3;
}
